.header-section {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.header-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 12px;
  border-bottom: 1px solid var(--glass-border);
}

.section-title {
  display: flex;
  align-items: center;
  gap: 12px;
  margin: 0;

  .icon-pulse {
    font-size: 1.4em;
    animation: pulse 3s infinite ease-in-out;
  }

  .title-text {
    font-size: 1.5rem;
    font-weight: 800;
    background: linear-gradient(135deg, var(--c-text-main) 0%, var(--c-text-sub) 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
}

.header-actions {
  display: flex;
  gap: 8px;
}

.icon-btn {
  width: 36px;
  height: 36px;
  border-radius: 50%;
  border: 1px solid var(--glass-border);
  background: var(--glass-panel);
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s var(--ease-spring);
  font-size: 1.1rem;

  &:hover {
    background: var(--c-primary);
    border-color: var(--c-primary);
    transform: translateY(-2px);
    box-shadow: 0 4px 12px var(--glass-shadow);

    span {
      filter: brightness(2);
    }
  }

  &.spinning span {
    animation: spin 1s linear infinite;
  }
}

/* ä¿¡æ¯å¸ƒå±€ï¼šå·¦ä¾§ä¿¡æ¯ï¼Œå³ä¾§ç…§ç‰‡ */
.info-layout {
  display: grid;
  grid-template-columns: 2fr 1fr; /* è°ƒæ•´æ¯”ä¾‹ï¼šä¿¡æ¯2/3ï¼Œç…§ç‰‡1/3 */
  gap: 24px;
  align-items: start;

  @media (max-width: 700px) {
    grid-template-columns: 1fr;
    grid-template-areas:
      'photo'
      'info';

    .photo-section {
      grid-area: photo;
      flex-direction: row;
      align-items: center;
      justify-content: flex-start;
      gap: 16px;
    }
    .global-info-bar {
      grid-area: info;
    }

    .photo-frame {
      width: 100px;
      height: 100px;
      aspect-ratio: 0.67;
    }
  }
}

.global-info-bar {
  display: flex;
  flex-direction: column;
  gap: 16px; /* å¢å¤§å¡ç‰‡é—´è· */
}

.info-item {
  display: flex;
  align-items: center;
  gap: 14px; /* å¢å¤§å›¾æ ‡ä¸æ–‡å­—é—´è· */
  padding: 14px 18px; /* å¢å¤§å†…è¾¹è· */
  background: var(--glass-bg);
  border-radius: var(--radius-md);
  border: 1px solid var(--glass-border);
  transition: transform 0.2s;

  &:hover {
    transform: translateX(4px);
    background: var(--glass-highlight);
  }

  .icon {
    font-size: 1.4rem;
  }

  .info-content {
    display: flex;
    flex-direction: column;
  }

  .label {
    font-size: 0.7rem;
    color: var(--c-text-mute);
    text-transform: uppercase;
    letter-spacing: 0.05em;
  }

  .text {
    font-weight: 600;
    color: var(--c-text-main);
  }
}

/* ç…§ç‰‡åŒºåŸŸä¼˜åŒ– */
.photo-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 14px; /* å¢å¤§é—´è· */
  padding-top: 4px; /* é¡¶éƒ¨ç•™ç™½ */
}

.photo-frame {
  width: 100%;
  /* âœ… é€‚é…ç«–å›¾ï¼š848*1264 â‰ˆ 0.67æ¯”ä¾‹ */
  aspect-ratio: 0.67;
  max-height: 450px; /* å¢å¤§æœ€å¤§é«˜åº¦é™åˆ¶ */
  border-radius: var(--radius-lg);
  overflow: hidden;
  border: 3px solid var(--glass-border);
  position: relative;
  background: var(--glass-panel);
  box-shadow: var(--glass-shadow);
  transition: all 0.3s;

  &.has-photo {
    border-color: var(--c-primary);
  }

  .char-photo {
    width: 100%;
    height: 100%;
    /* âœ… é‡è¦ï¼šcontain ä¿æŒå›¾ç‰‡å®Œæ•´æ˜¾ç¤ºï¼Œcover å¡«å……å®¹å™¨ */
    object-fit: contain;
    transition: transform 0.5s;
  }

  &:hover .char-photo {
    transform: scale(1.05);
  }

  .photo-placeholder {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: var(--c-text-mute);
    background: linear-gradient(135deg, var(--glass-bg) 0%, var(--glass-panel) 100%);

    .placeholder-icon {
      font-size: 3rem;
      opacity: 0.4;
    }
    .placeholder-text {
      font-size: 0.75rem;
      font-weight: 700;
      margin-top: 8px;
      opacity: 0.6;
      letter-spacing: 0.1em;
    }
  }

  .loading-spinner {
    position: absolute;
    inset: 0;
    background: rgba(255, 255, 255, 0.5);
    backdrop-filter: blur(2px);
    z-index: 2;
    &::after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      width: 20px;
      height: 20px;
      border: 2px solid var(--c-primary);
      border-top-color: transparent;
      border-radius: 50%;
      animation: spin 1s linear infinite;
      margin: -10px 0 0 -10px;
    }
  }
}

.photo-caption {
  display: flex;
  align-items: center;
  gap: 8px;

  .char-name {
    font-weight: 700;
    font-size: 1rem; /* å¢å¤§å­—ä½“ */
    color: var(--c-text-main);
  }

  .char-status-dot {
    width: 8px;
    height: 8px;
    border-radius: 50%;
    background: var(--c-text-mute);

    &.active {
      background: hsl(var(--hue-success) 80% 60%);
      box-shadow: 0 0 8px hsl(var(--hue-success) 80% 60%);
    }
  }
}

/* æ ‡ç­¾é¡µ */
.tabs-nav {
  display: flex;
  gap: 8px;
  overflow-x: auto;
  padding: 4px;

  &::-webkit-scrollbar {
    height: 0;
  }

  .tab-button {
    padding: 6px 16px;
    border-radius: 20px;
    border: 1px solid transparent;
    background: transparent;
    color: var(--c-text-sub);
    font-size: 0.9rem;
    cursor: pointer;
    white-space: nowrap;
    transition: all 0.3s;

    &:hover {
      background: var(--glass-highlight);
    }

    &.active {
      background: var(--c-primary);
      color: white;
      box-shadow: 0 4px 12px hsla(var(--hue-primary), 80%, 60%, 0.3);
      font-weight: 600;
    }
  }
}

/* ğŸ“ æ ‡é¢˜åŒºåŸŸå¾®è°ƒ */
.dashboard-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: calc(var(--space-unit) * 1.5);
  padding-bottom: var(--space-unit);
  border-bottom: 1px solid var(--glass-border);

  h2 {
    margin-bottom: 0;
  }

  .badge {
    font-size: 0.85rem;
    padding: 4px 12px;
    background: var(--c-primary);
    color: white;
    border-radius: 20px;
    font-weight: 600;
    box-shadow: 0 4px 12px hsla(var(--hue-primary), 60%, 50%, 0.3);
  }
}

/* ğŸ•¸ï¸ æ ¸å¿ƒç½‘æ ¼å¸ƒå±€ */
.info-grid {
  display: grid;
  /* å“åº”å¼åˆ—å®½ï¼šæœ€å° 280pxï¼Œè‡ªåŠ¨å¡«æ»¡ï¼Œç¡®ä¿ä¸¤åˆ—å¸ƒå±€ */
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  /* ç»Ÿä¸€é—´è· */
  gap: var(--space-unit);
  /* è‡ªåŠ¨å¯¹é½è¡Œé«˜ */
  align-items: stretch;
}

/* ä¸¤åˆ—å¹³åˆ†å¸ƒå±€ä¸“ç”¨æ ·å¼ */
.two-column-split {
  /* åœ¨æ¡Œé¢ç«¯å¼ºåˆ¶å¹³åˆ†ç¬¬ä¸€è¡Œå®½åº¦ */
  /* ä½¿ç”¨åª’ä½“æŸ¥è¯¢ç¡®ä¿ç§»åŠ¨ç«¯å †å  */
}

/* æ¡Œé¢ç«¯ï¼šçŠ¶æ€é¢æ¿å’Œå¤–è§‚ç‰¹å¾å§‹ç»ˆå¹³åˆ†å®½åº¦ */
@media (min-width: 600px) {
  .info-grid .card.two-column-split:first-child {
    grid-column: 1;
  }

  .info-grid .card.two-column-split:last-child {
    grid-column: 2;
  }
}

/* ç§»åŠ¨ç«¯ï¼šä¸¤ä¸ªå¡ç‰‡å †å ï¼Œå„å å…¨å®½ */
@media (max-width: 599px) {
  .info-grid .card.two-column-split {
    grid-column: 1 / -1;
  }
}

/* ğŸƒ é€šç”¨å¡ç‰‡æ ·å¼ - ä¼˜åŒ–ç‰ˆ */
.card {
  /* ç»ç’ƒæ‹Ÿæ€èƒŒæ™¯ (æ¯”å®¹å™¨ç¨æ·±æˆ–ç¨æµ…ä»¥åŒºåˆ†å±‚çº§) */
  background: var(--glass-bg);
  border: 1px solid var(--glass-border);
  border-radius: var(--radius-md);

  /* å¸ƒå±€æ§åˆ¶ */
  padding: 16px;
  display: flex;
  flex-direction: column;
  justify-content: center;

  /* é«˜åº¦æ§åˆ¶ï¼šç¡®ä¿è§†è§‰é¥±æ»¡ */
  min-height: 140px; /* ç»Ÿä¸€æœ€å°é«˜åº¦ */
  height: 100%; /* å¡«æ»¡ Grid å•å…ƒæ ¼é«˜åº¦ */

  /* åŠ¨æ•ˆ */
  transition: all 0.3s var(--ease-spring);
  position: relative;
  overflow: hidden;

  /* æ‚¬åœå¾®äº¤äº’ */
  &:hover {
    transform: translateY(-4px) scale(1.01);
    background: var(--glass-highlight);
    box-shadow: 0 12px 24px -8px hsla(220, 20%, 10%, 0.1);
    border-color: var(--c-primary);
    z-index: 1;
  }

  /* å˜ä½“ï¼šå…¨å®½å¡ç‰‡ (è·¨è¶Šæ‰€æœ‰åˆ—) */
  &.full-width {
    grid-column: 1 / -1;
    min-height: auto; /* æ ¹æ®å†…å®¹è‡ªé€‚åº”é«˜åº¦ */
    padding: calc(var(--space-unit) * 1.5);
  }

  /* å†…éƒ¨æ’ç‰ˆ */
  h3 {
    font-size: 0.85rem;
    color: var(--c-text-mute);
    margin: 0 0 12px 0;
    text-transform: uppercase;
    letter-spacing: 0.05em;
    font-weight: 600;
  }

  /* æ•°æ®å¼ºè°ƒæ ·å¼ */
  .value {
    font-family: var(--font-sans);
    font-size: 2.2rem;
    font-weight: 800;
    background: linear-gradient(135deg, var(--c-text-main) 0%, var(--c-primary) 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    line-height: 1.1;
  }
}

/* âœ¨ å˜ä½“ï¼šé«˜äº®å¡ç‰‡ (ç”¨äº HP/MP ç­‰æ ¸å¿ƒæ•°æ®) */
.card.highlight {
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  background: linear-gradient(135deg, var(--glass-panel) 0%, hsla(var(--hue-primary), 30%, 90%, 0.4) 100%);

  .card-icon {
    font-size: 2.5rem;
    opacity: 0.8;
    filter: drop-shadow(0 4px 8px rgba(0, 0, 0, 0.1));
  }

  .card-data {
    text-align: right;
  }
}

/* ğŸŒ‘ æ·±è‰²æ¨¡å¼é€‚é… */
:root[data-theme='dark'] .status-bar-container {
  .card:hover {
    background: hsla(220, 20%, 20%, 0.6);
  }

  .card.highlight {
    background: linear-gradient(135deg, var(--glass-panel) 0%, hsla(var(--hue-primary), 30%, 20%, 0.4) 100%);
  }
}

/* ğŸ‘¥ äººç‰©é€‰æ‹©å¡ç‰‡ä¸“ç”¨æ ·å¼ */
.character-selector-card {
  .tabs-nav {
    margin-top: 8px;
    margin-bottom: 0;
  }
}

.text-box {
  padding: 12px;
  border-radius: var(--radius-sm);
  font-size: 0.95rem;
  line-height: 1.6;

  &.thought {
    background: var(--bg-thought);
    color: var(--text-thought);
    border-left: 3px solid var(--text-thought);
  }

  &.action {
    background: var(--bg-action);
    color: var(--text-action);
    font-style: italic;
    border-left: 3px solid var(--text-action);
  }
}

.detail-list,
.appearance-list {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.detail-item,
.appearance-item {
  display: flex;
  justify-content: space-between;
  padding: 4px 0;
  border-bottom: 1px dashed var(--glass-border);

  &:last-child {
    border-bottom: none;
  }

  /* å·¦ä¾§æ ‡ç­¾ï¼šå­—ä½“æ›´å°ã€é¢œè‰²æ›´æ·¡ */
  .label,
  .appearance-label {
    font-size: 0.85rem;
    color: var(--c-text-mute);
    font-weight: 500;
    line-height: 1.4;
  }

  /* å³ä¾§æ•°å€¼/æè¿°ï¼šå­—ä½“æ›´å¤§ã€é¢œè‰²æ›´æ·±ã€æ›´çªå‡º */
  .value,
  .appearance-text {
    font-size: 1rem;
    font-weight: 600;
    text-align: right;
    line-height: 1.4;
  }

  /* çŠ¶æ€é¢æ¿çš„æ•°å€¼ä½¿ç”¨å‘å…‰å­—ä½“ */
  .value {
    background: linear-gradient(135deg, var(--c-text-main) 0%, var(--c-primary) 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }

  /* å¤–è§‚ç‰¹å¾çš„æ–‡æœ¬ä¹Ÿä½¿ç”¨å‘å…‰å­—ä½“ */
  .appearance-text {
    background: linear-gradient(135deg, var(--c-text-main) 0%, var(--c-primary) 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
}

/* ç©ºçŠ¶æ€ */
.empty-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 40px;
  color: var(--c-text-mute);

  .empty-icon {
    font-size: 3rem;
    margin-bottom: 10px;
    opacity: 0.5;
  }
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}
@keyframes pulse {
  0%,
  100% {
    opacity: 1;
  }
  50% {
    opacity: 0.5;
  }
}
